@mixin blueGradient( $degree ) {
    background: #3c78f0; /* Old browsers */
    background: -moz-linear-gradient( $degree, #3c78f0 0%, #64eaff 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient( $degree, #3c78f0 0%, #64eaff 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient( if( ($degree < 0), (180deg + $degree), $degree ), #3c78f0 0%, #64eaff 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3c78f0', endColorstr='#64eaff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

